/*
  NOTE: Unused shadows depths are left commented out for future reference
*/

.sanity-shadow-0dp {
  box-shadow: none;
}

.sanity-shadow-1dp {
  box-shadow:
    0 1px 1px 0 var(--color-shadow-umbra),
    0 2px 1px -1px var(--color-shadow-penumbra),
    0 1px 3px 0 var(--color-shadow-ambient);
}

